/* @author Bilal Cinarli */

/** -------------------------------------------
    Colors
    ------------------------------------------- **/
/**
 * Named Colors
 *
 * Some named colors for inheriting colors over different variables
 */
$color-primary: #009bdc !default;
$color-secondary: #67af00 !default;
$color-tertiary: #ffa900 !default;
$color-cancel: #e87352 !default;
$color-error: $color-cancel !default;
$color-text: #222 !default;
$color-light-text: #999 !default;
$color-light-gray: #eee !default;
$color-gray: #c6c6c6 !default;
$color-dark-gray: #a6a6a6 !default;
$color-white: #fff !default;
$color-black: #000 !default;

/**
 * Simple colors for general styling
 */
$base-color: $color-text !default;
$base-anchor-color: $color-text !default;
$base-anchor-highlight: $color-primary !default;

/**
 * Border Colors
 *
 * Colors applied to border in focus, disabled, readonly and stationary state
 * @default $base-border-color: #ccc;
 */
$base-border-color: $color-gray !default;
$base-border-highlight: $color-dark-gray !default;
$base-border-disabled: $color-light-gray !default;
$base-border-readonly: $color-gray !default;

/**
 * Field Background Colors
 *
 * Color applied to field background in focus, disabled, readonly and stationary state
 */
$base-field-background: $color-white !default;
$base-field-highlight: $color-white !default;
$base-field-disabled: $color-gray !default;
$base-field-readonly: $color-light-gray !default;

/**
 * Button colors
 */
/**
 * Default Button
 *
 * Unstyled, normal button
 */
$base-button-color: $color-gray !default;
$base-button-highlight-color: null !default;
$base-button-text-color: $color-text !default;

/**
 * Disabled Button
 */
$base-disabled-color: $color-light-gray !default;
$base-disabled-highlight-color: $color-light-gray !default;
$base-disabled-text-color: $color-dark-gray !default;

/**
 * Primary Action Button
 *
 * Maybe used for call-to-action button or/and save/submit buttons in forms
 */
$base-primary-color: $color-primary !default;
$base-primary-highlight-color: null !default;
$base-primary-text-color: $color-white !default;

/**
 * Secondary Action Button
 *
 * Useful for indication secondary action in a form near the primary action button
 */
$base-secondary-color: $color-secondary !default;
$base-secondary-highlight-color: null !default;
$base-secondary-text-color: $color-white !default;

/**
 * Tertiary Action Button
 *
 * Useful for actions whether they are not fit for primary or secondary
 */
$base-tertiary-color: $color-tertiary !default;
$base-tertiary-highlight-color: null !default;
$base-tertiary-text-color: $color-white !default;

/**
 * Cancel Action Button
 *
 * Simply used for cancel button for a form or call-to-action, confirmation etc.
 */
$base-cancel-color: $color-cancel !default;
$base-cancel-highlight-color: null !default;
$base-cancel-text-color: $color-white !default;

/**
 * Color maps
 */
$base-colors: (
        'standart': $base-button-color,
        'standart-highlight': $base-button-highlight-color,
        'standart-text': $base-button-text-color,
        'disabled': $base-disabled-color,
        'disabled-highlight': $base-disabled-highlight-color,
        'disabled-text': $base-disabled-text-color,
        'primary': $base-primary-color,
        'primary-highlight': $base-primary-highlight-color,
        'primary-text': $base-primary-text-color,
        'secondary': $base-secondary-color,
        'secondary-higlight': $base-secondary-highlight-color,
        'secondary-text': $base-secondary-text-color,
        'tertiary': $base-tertiary-color,
        'tertiary-highlight': $base-tertiary-highlight-color,
        'tertiary-text': $base-tertiary-text-color,
        'cancel': $base-cancel-color,
        'cancel-higlight': $base-cancel-highlight-color,
        'cancel-text': $base-cancel-text-color
);



